	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Greetings from Arizona","date":"29 Sep 2000","slug":"/2000-09-29-greetings-from-arizona/","tags":"Y2000"},"html":"<p>We made it to Arizona at midnight Thursday morning. The temperature at that time (over 90F) was warmer than the entire summer in Hamburg.</p>\n<p>Christopher travelled much better than either of us, as I expected. We had normal tourist-class seats with Christopher on my lap. He slept, I couldn't move.</p>\n<p>After spending yesterday in jet-lag limbo, we're going to try to enjoy the sun today. We had Mexican food last night, tomorrow night we'll go to our first major league baseball game in over 6 years. It's little things like that that I miss. But not often.</p>"},"next":{"fields":{"title":"Pack Day","date":"26 Sep 2000","slug":"/2000-09-26-pack-day/"}},"previous":{"fields":{"title":"Ballyard","date":"01 Oct 2000","slug":"/2000-10-01-ballyard/"}}},{"node":{"fields":{"title":"Pack Day","date":"26 Sep 2000","slug":"/2000-09-26-pack-day/","tags":"Y2000"},"html":"<p>Today we pack, tomorrow we fly. Mama is already asking why I'm waiting until day. She claims to already be done with her things. </p>\n<p>We're trying to stick to a single suitcase of clothes for the two of us. Christopher gets 3, with all his toys, sleep material, accesories, formula (as far as I can tell, hypo-allegenic formula is not available in the US except from a physician), food and diapers for the first couple of days.</p>\n<p>Hopefully everything will work for the camera. We only have two compact flash cards, and no laptop, so we have to find a way to upload somehow. We'll bring all the cables and the card reader. We have two sets of rechargable batteries, but the charger only runs on 230 V. I also burned a fresh CD with pictures... over 1500 files.</p>"},"next":{"fields":{"title":"Behind the Curtain","date":"24 Sep 2000","slug":"/2000-09-24-behind-the-curtain/"}},"previous":{"fields":{"title":"Greetings from Arizona","date":"29 Sep 2000","slug":"/2000-09-29-greetings-from-arizona/"}}},{"node":{"fields":{"title":"Behind the Curtain","date":"24 Sep 2000","slug":"/2000-09-24-behind-the-curtain/","tags":"Y2000"},"html":"<p>Here's <a href=\"http://shanson.editthispage.com/24hours\">what we did last Sunday</a>, a photo gallery that is part of the <a href=\"http://array.editthispage.com/24hours\">Behind the Curtain</a> project.</p>\n<p><a href=\"http://www.zopesite.com/behindthecurtain\"><img src=\"http://www.arrayweb.com/btc/english.jpg\" height=\"100\" width=\"100\" border=\"0\" alt=\"Behind the Curtain: a day in the life of webloggers\" /></a> <a href=\"http://shanson.editthispage.com/24hours\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/09/btc.jpg\" height=\"100\" width=\"100\" border=\"0\" alt=\"btc.jpg: \" /></a></p>\n<hr />\n<p>This Sunday, in between packing and dealing with teething pain, we went to our 2nd 'Krabbelgottesdienst', a 'crawling' church service for toddlers, to celebrate the harvest. Christopher actually paid attention and seemed to enjoy the singing and the hand-clapping, foot-stomping parts.</p>"},"next":{"fields":{"title":"Relief!","date":"23 Sep 2000","slug":"/2000-09-23-relief/"}},"previous":{"fields":{"title":"Pack Day","date":"26 Sep 2000","slug":"/2000-09-26-pack-day/"}}}],"pathPrefix":"","first":false,"last":false,"index":1041,"pageCount":1101}}

